<template>
	<div class="wangEditor">
		<!-- 弹出层 -->
		<!-- <van-popup v-model="publish_Waiting_response">
			<span style='backgroundColor:red'>文章发布中...</span>
		</van-popup> -->
    <!-- 基础 -->
    <!-- 输入任意文本 -->
    <van-field v-model="title"  placeholder="请输入文章标题…" maxlength="50" class="articleTit"
    autosize show-word-limit type="textarea" />
		<div ref="toolBar" class="toolBar" ></div>
		<div ref="editor" class="editor"></div>
<!-- ///////////////////////////////////////////////////////////////////// -->
<van-field
  readonly
  clickable
  label="文章分类"
  :value="showValue"
  placeholder="选择分类"
  @click="showPicker = true"
/>

<van-popup v-model="showPicker" position="bottom">
  <van-picker :columns="columns" @change="onChange"
  show-toolbar @confirm='onConfirm' @cancel="showPicker = false"/>
</van-popup>
<!-- ///////////////////////////////////////////////////////////////////////// -->
 <!-- <div class="publish" @click='publish' >
     <span v-show="!ws_publish">立即发布</span>
     <span v-show="ws_publish">发布中...</span>
  </div> -->
  <van-button type="primary" size="large" @click='publish' class="publish">
    <span v-show="!ws_publish">立即发布</span>
    <span v-show="ws_publish">发布中...</span>
  </van-button>
  <!-- <van-button type="primary" size="large" @click="publish">立即发布</van-button>
   -->
	</div>
</template>

<script>
  // const citys = {
  //   '王者荣耀': ['全部', '宁波', '温州', '嘉兴', '湖州'],
  //   '和平精英': ['全部', '厦门', '莆田', '三明', '泉州'],
  //   'qq飞车': ['全部', '2', '3', '4', '5'],
  //   '穿越火线': ['全部', '2', '3', '4', '5']
  // };
  const citys = {
    '百科': ['全部', '宇宙探索', '自然地理', '科学原理', '释疑解惑', '人文科学'],
    '军事': ['全部', '军事科技', '讲武堂', '人物志', '军民融合','网络硝烟','战场还原'],
    '科幻': ['全部', '名家动态', '新闻探索', '影视作品', '科普文创'],
    '人物': ['全部', '走进大师', '名言警句', '精彩人生'],
    '手机': ['全部', '华为', '小米', 'oppo']
  };
	import {createNamespacedHelpers} from 'vuex'
  const readAndEditor=createNamespacedHelpers('readAndEditor')
	const login=createNamespacedHelpers('login')
	// import { Message } from "element-ui";
	import E from 'wangeditor'
	export default {
		name: 'wangEditor',
		data(){
			return {
				editor : '',
				//editorContent : "" ,//内容
        //文章标题
				title : "",
        showPicker : false,
        ///////////////////////
        columns: [
                {
                  values: Object.keys(citys),
                  className: 'column1'
                },
                {
                  values: citys['百科'],
                  className: 'column2',
                  defaultIndex: 0
                }
              ],
        //////////////////////////
        // 选项
        showValue : '',
        finalValue : '',
        // 发布等待响应
        // publish_Waiting_response : false,
        ws_publish:false,
        // 文章的图片src
        // imgsSrc : []
			}
		},
		// computed:{

		// 	...readAndEditor.mapState({
		// 		moBoolean : state => state.moBoolean
		// 	})
  //     ...readAndEditor.mapState({
  //     	articleContent : state => state.articleContent
  //     })
		// },
		methods: {
      //修改部分用户信息
      ...login.mapMutations({
            	setHandler: 'setHandler'
            }),
      //阅读页和编辑页
      ...readAndEditor.mapMutations({
            	moBooleanHandler: 'moBooleanHandler'
            }),
      ...readAndEditor.mapMutations({
            	articleContentHandler: 'articleContentHandler'
            }),
      onChange(picker, values){
          picker.setColumnValues(1, citys[values[0]]);
      },
      onConfirm(values){
        console.log(values)
        this.showValue = values[0] +'  板块下的  '+ values[1]
        this.finalValue = values
        this.showPicker = false
      },
			// 提交文章
			publish(){
              //判断是否登录
              if(!!this.hasLogin == false){
                this.$toast("您尚未登录")
                return;
              }

							if(!!this.finalValue == false){
                this.$toast("请选择文章分类")
							  return ;
							}
							if(!!this.title == false){
              this.$toast("请输入文章标题")
							return ;
							}
							if(!!this.editor.txt.text() == false){
                 this.$toast("请输入文章内容")
								return ;
							}
            if(!!this.ws_publish == true){
              return;
            }
            this.ws_publish = true
            this.isDoing = true
            if(!!this.moBoolean == true){
              //////////////////////////////修改 editor.txt.html()
              var params = {
              	title : this.title,
              	// editorContent : this.editorContent,
                editorContent : this.editor.txt.html(),
              	finalValue : this.finalValue,
                articleId : this.articleContent.article_id
              }
              console.log('修改文章',this.editor.txt.html())
              this.$api.article.moPublish(params).then(
                  res =>{
                    this.isDoing = false
                    this.ws_publish = false
                    /* 修改*/
                    if(res.data.admin_stop){
                      res.data.admin_stop = this.$formatDate(new Date(res.data.admin_stop))
                      this.$toast(`您被管理封号至${res.data.admin_stop},请注意言行`)
                    }else if(res.data=='success'){
                      this.$toast("文章修改成功")
                    }
                    /* */
                      // switch(res.data){
                      //   case "success":
                      //       this.$toast("文章修改成功")
                      //       // this.$router.replace({
                      //       //   path:'/'
                      //       // })
                      //       break;
                      // }
                  }
              )
            }else{
              ////////////////////////////////发布
              var params = {
              	title : this.title,
                /* 文章中需要的图片链接*/
                // imgsSrc : this.imgsSrc,
                editorContent : this.editor.txt.html(),
              	finalValue : this.finalValue
              }
              this.$api.article.publish(params).then(
                  res =>{
                    console.log(res.data,'res.data')
                    this.isDoing = false
                    this.ws_publish = false
                    if(res.data.admin_stop){
                      res.data.admin_stop = this.$formatDate(new Date(res.data.admin_stop))
                      this.$toast(`您被管理封号至${res.data.admin_stop},请注意言行`)
                    }else if(res.data == 'no_allow_pub'){
                      this.$toast("请歇一歇再来发帖吧")
                    }else if(res.data=='success'){
                      this.$toast("文章发布成功")
                      this.setHandler(['user_article_count','add'])
                    }
                    /* */
                      // switch(res.data){
                      //   case "success":
                      //       this.$toast("文章发布成功")
                      //       this.setHandler(['user_article_count','add'])
                      //       // this.$router.replace({
                      //       //   path:'/'
                      //       // })
                      //       break;
                      //   case "no_allow_pub":
                      //       this.$toast("请歇一歇再来发帖吧")
                      //       break;
                      // }
                  }
              )
            }

						// var params = {
						// 	title : this.title,
						// 	editorContent : this.editorContent,
						// 	finalValue : this.finalValue
						// }
      //       this.$api.article.publish(params).then(
      //           res =>{
      //             this.publish_Waiting_response = false
      //               switch(res.data){
      //                 case "发布成功":
      //                     this.$tip("文章发布成功","success")
      //                     break;
      //               }
      //           },
      //           ()=>{
      //             this.$tip("请求失败","warning")
      //           }
      //       )

			},
      // DataURL 转换为 File
      dataURLtoFile(dataurl,filename){
        var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
                bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        		while(n--){
        			u8arr[n] = bstr.charCodeAt(n);
        		}
        		return new File([u8arr], filename, {type:mime});
      },
			//监听上传图片的几个阶段，和做相应的处理
			toListenUp(editor){
        var that = this;
				editor.customConfig.uploadImgHooks = {
                    before: function (xhr, editor, files) {
             //          console.log(editor,files[0],'files12323435457568677878978')
             //          var reader = new FileReader();
             //          reader.readAsDataURL(files[0]);
             //          reader.onload = (e)=> {

             //            var image = new Image();
             //            image.src =e.target.result
             //            image.onload = ()=> {
             //             var w = image.width * 1,
             //                 h = image.height,
             //                 quality = 0.1, //图像质量
             //                 canvas = document.createElement('canvas'),
             //                 context = canvas.getContext('2d');

             //                 canvas.width = w;
             //                 canvas.height = w * (image.height / image.width);
             //                 context.drawImage(image, 0, 0, canvas.width, canvas.height);
             //                 var finalDataUrl = canvas.toDataURL(files[0].type,quality);

             //                 files[0] = that.dataURLtoFile(finalDataUrl,files[0].name)


             //            }
             //          }

                        // 图片上传之前触发
                        // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象，files 是选择的图片文件

                        // 如果返回的结果是 {prevent: true, msg: 'xxxx'} 则表示用户放弃上传
                        // return {
                        //     prevent: true,
                        //     msg: '放弃上传'
                        // }
                    },
                    success: function (xhr, editor, result) {
                        // 图片上传并返回结果，图片插入成功之后触发
                        // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象，result 是服务器端返回的结果
                    },
                    fail: function (xhr, editor, result) {
                        // 图片上传并返回结果，但图片插入错误时触发
                        // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象，result 是服务器端返回的结果
                    },
                    error: function (xhr, editor) {
                        // 图片上传出错时触发
                        // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象
                    },
                    timeout: function (xhr, editor) {
                        // 图片上传超时时触发
                        // xhr 是 XMLHttpRequst 对象，editor 是编辑器对象
                    },

                    // 如果服务器端返回的不是 {errno:0, data: [...]} 这种格式，可使用该配置
                    // （但是，服务器端返回的必须是一个 JSON 格式字符串！！！否则会报错）
                    customInsert: function (insertImg, result, editor) {
                        // 图片上传并返回结果，自定义插入图片的事件（而不是编辑器自动插入图片！！！）
                        // insertImg 是插入图片的函数，editor 是编辑器对象，result 是服务器端返回的结果// 举例：假如上传图片成功后，服务器端返回的是 {url:'....'} 这种格式，即可这样插入图片：
                        var url = result.data[0] //获取后台返回的图片路径
                        insertImg(url)//把图片路径展示在编辑器里面
                        // result 必须是一个 JSON 格式字符串！！！否则报错
                    }
                }
            }


		},
		watch:{
			// editorContent : {
			// 	handler(newValue, old){
			// 		console.log("新值为:"+ newValue)
			// 	},
			// 	immediate: true
			// }

		},

		mounted () {
      var that = this
			var editor = new E(this.$refs.toolBar,this.$refs.editor)
      this.editor = editor

			editor.customConfig = {
				// onchange : (html) =>{
    //       this.imgsSrc.length = 0
    //       for(let j= 0; j<this.$refs.editor.getElementsByTagName("img").length;j++){
    //         this.imgsSrc.push(this.$refs.editor.getElementsByTagName("img")[j].src)
    //       }
    //       console.log(this.imgsSrc,'imgs')
    //     },// 内容
        uploadImgShowBase64 : false,// base 64 存储图片
				//uploadImgServer :'http://localhost:80/editorImages',
         //'http://localhost:80/wangeditor/upload',// 配置上传服务器端地址的接口
				uploadImgMaxLength : 1 , // 限制一次最多上传 1 张图片
        uploadImgMaxSize : 500 * 1024 * 1024,//将图片大小限制为 500M
				//uploadFileName : 'uploadImg' , //设置上传图片文件的时候，后台接受的文件名，files.uploadImg;
				withCredentials : false , //跨域上传中如果需要传递 cookie 需设置 withCredentials
				uploadImgTimeout : 3000,  //自定义 timeout 时间，这里是设置的3秒
        zIndex : 0,
        menus : [
                    'head',  // 标题
                    'bold',  // 粗体
                    // 'fontSize',  // 字号
                    // 'fontName',  // 字体
                    //'italic',  // 斜体
                    //'underline',  // 下划线
                    //'strikeThrough',  // 删除线
                    //'foreColor',  // 文字颜色
                    //'backColor',  // 背景颜色
                    'link',  // 插入链接
                    'list',  // 列表
                    //'justify',  // 对齐方式
                    'quote',  // 引用
                    'emoticon',  // 表情
                    'image',  // 插入图片
                    'table',  // 表格
                    //'video',  // 插入视频
                    //'code',  // 插入代码
                    //'undo',  // 撤销
                    //'redo'  // 重复
                ],
        customUploadImg : (files, insert)=>{
          //判断是否登录
          if(!!this.hasLogin == false){
            this.$toast("您尚未登录")
            return;
          }
          console.log('未处理', files[0])
          /* 计算图像压缩比例**********************/
          let whatKB = files[0].size/1024;//图像多少kB
          console.log(whatKB,'whatKB')
          let quality;//图像质量
          if(whatKB>50){//图像质量大于50kB 压缩
            quality = 50 / whatKB
            console.log(quality,'压缩')
          }else{//小于等于50kB 不压缩
            quality = undefined
          }
          /////////////////////////////////////////////////////
          var reader = new FileReader();//创建文件阅读实例
          reader.readAsDataURL(files[0]);// 1 将file 对象转成 Data URL
          reader.onload = (e)=> {//文件阅读实例读取文件成功完成时触发
            // var finalFlie;
            var image = new Image();//创建图像对象
            image.src =e.target.result//2 图像对象的src 等于 文件的路径
            image.onload = ()=> {//图像加载完成时触发
             var w = image.width * 1,
                 h = image.height,
                 //quality = 0.1, //图像质量
                 canvas = document.createElement('canvas'),//创建canvas元素
                 context = canvas.getContext('2d');
                  /* 定义变量结束*/
                 canvas.width = w;
                 canvas.height = w * (image.height / image.width);
                 context.drawImage(image, 0, 0, canvas.width, canvas.height);//3 根据Image对象画canvas
                 //var finalDataUrl = canvas.toDataURL(files[0].type,quality);//"image/jpeg"
                 var finalDataUrl = canvas.toDataURL("image/jpeg",quality);//4 canvas转换成Data URL
                 files[0] = that.dataURLtoFile(finalDataUrl,files[0].name)//5 将Data URL 转换成 文件流
                 // 后端接口
                 let formData = new FormData()//创建一个表单对象 用key/value提交文件
                 formData.append('uploadImg',files[0])//表单对象附加key/value文件
                 this.$api.article.editorImg(formData).then(
                     res =>{
                         switch(res.data){
                            default:
                            insert(res.data.imgUrl)
                            this.$toast("图片上传成功")
                            /* ***************/
                            /* ***************/
                            /* default结束*/
                         }

                     }
                 )
                 // 后端接口
            }

          }
          // console.log('files--------------', files[0])
          /////////////////////////////////////////////////////
        },
			}
			 this.toListenUp(editor);//监听上传的各个阶段
			// 创建富文本编辑器
			editor.create()
      /* */

      /* */
      /////////////////////////////////如果是修改文章 article_content
      if(!!this.moBoolean == true){
        editor.txt.html(this.articleContent.article_contentHTML)
        this.title = this.articleContent.article_title
      }
      console.log(this.articleContent.article_contentHTML)
		},
    computed:{
      isDoing:{
              get () {
                    return this.$store._modulesNamespaceMap['win/'].context.state.isDoing
              },
              set (value) {
                this.$store._modulesNamespaceMap['win/'].context.commit('isDoingHandler', value)
              }
            },
      ...login.mapState({
           	hasLogin : state => state.hasLogin
           }),
    	...readAndEditor.mapState({
    		moBoolean : state => state.moBoolean
    	}),
      ...readAndEditor.mapState({
      	articleContent : state => state.articleContent
      })
    },
    beforeRouteLeave( to,from,next ){
      this.moBooleanHandler(false)
      this.articleContentHandler('')
      next();  //写了这个，才会从当前导航离开，否则会一直停留在当前导航页
    }
	}
</script>

<style>
  .w-e-toolbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
</style>

<style scoped lang='scss'>
	@import "../../assets/scss/editor/editor.scss";
</style>



